.gpt_navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 6rem;
}

.gpt_navbar-links {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.gpt_navbar-links_logo {
    margin-right: 2rem;
}

.gpt_navbar-links_logo img {
    width: 62.56px;
    height: auto;
}

.gpt_navbar-links_containers {
    display: flex;
    flex-direction: row;
}

.gpt_navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.gpt_navbar-links_containers p,
.gpt_navbar-sign p,
.gpt_navbar-menu_container p {
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
}

.gpt_navbar-sign button,
.gpt_navbar-menu_container button {
    padding: 0.5rem 1rem;
    flex-shrink: 0;
    color: #FFF;
    background-color: var(--color-subtext);
    font-family: var(--font-family);
    font-size: 18px;
    font-weight: 500;
    border-radius: 0.3rem;
    border: 0;
    outline: none;
    cursor: pointer;
}

.gpt_navbar-menu {
    margin-left: 1rem;
    display: none;
    position: relative;
}

.gpt_navbar-menu svg {
    cursor: pointer;
}

.gpt_navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    text-align: end;
    background: var(--font-family);
    padding: 2rem;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
 }

.gpt_navbar-menu_container p {
    margin: 1rem 0rem;
 }

 .gpt_navbar-menu_container-links-sign {
    display: none;
 }

 @media  screen and (max-width: 1050px){
    .gpt_navbar-links_containers {
        display: none;
    }

    .gpt_navbar-menu {
        display: flex;
    }
 }

 @media  screen and (max-width: 700px) {
    .gpt_navbar {
        padding: 2rem 4rem;
    }
 }

 @media  screen and (max-width: 550px) {
    .gpt_navbar {
        padding: 2rem;
    }

    .gpt_navbar-sign {
        display: none;
    }

    .gpt_navbar-menu_container {
        top: 20px;
    }

    .gpt_navbar-menu_container-links-sign {
        display: block;
    }
 }