.gpt_possibility {
    display: flex;
    flex-direction: row  ;
}

.gpt_possibility-image {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-right: 2rem;
}

.gpt_possibility-image img {
    width: 100%;
    height: 100%;
}

.gpt_possibility-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
}

.gpt_possibility-content h4 {
    color: #71E5FF;
    font-family: var(--font-family);
    font-size: 16px;
    font-weight: 500;
    line-height: 30px; 
}
.gpt_possibility-content h4:last-child {
    color: #FF8A71
}
.gpt_possibility-content h1 {
    font-family: var(--font-family);
    font-size: 34px;
    font-weight: 800;
    line-height: 45px;
    margin: 1rem 0;
}
.gpt_possibility-content p {
    color: var(--color-text);
    font-family: var(--font-family);
    font-size: 16px;
    line-height: 30px;
    margin-bottom: 2rem;
}

@media screen  and (max-width: 950px) {
    .gpt_possibility {
        flex-direction: column;
    }

    .gpt_possibility-image {
        margin: 1rem 0;
    }

    .gpt_possibility-content {
        margin-top: 2rem;
    }
}