.gpt_footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: var(--color-footer);
}

.gpt_footer-heading {
    width: 100%;
    text-align: center;
    margin-bottom: 3rem;
}

.gpt_footer-heading h1 {
    font-family: var(--font-family);
    font-size: 62px;
    font-weight: 800;
    line-height: 75px;
    letter-spacing: -2.48px;
}

.gpt_footer-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    border: 1px solid #FFF;
    text-align: center;
    margin-bottom: 10rem;
    cursor: pointer;
}

.gpt_footer-btn p {
    font-family: var(--font-family);
    color: #FFF;
    font-size: 18px;
    font-weight: 400;
}

.gpt_footer-links {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    flex-direction: row;
    width: 100%;
    text-align: left;
}

.gpt_footer-links div {
    width: 250px;
    margin: 1rem;
}

.gpt_footer-links_logo {
    display: flex;
    flex-direction: column;
}

.gpt_footer-links_logo img {
    width: 100px;
    height: 70px;
    margin-bottom: 1rem;
}

.gpt_footer-links_logo p {
    color: #FFF;
    font-family: var(--font-family);
    font-size: 12px;
}

.gpt_footer-links_div {
    display: flex;
    justify-self: flex-start;
    flex-direction: column;
}

.gpt_footer-links_div h4 {
    color: #FFF;
    font-family: var(--font-family);
    font-size: 14px;
    line-height: 16px;
    margin-bottom: 1rem;
}

.gpt_footer-links_div p {
    color: #FFF;
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 16px;
    margin: 0.5rem 0;
    cursor: pointer;
}

.gpt_footer-copyrights {
    width: 100%;
    text-align: center;
    margin: 2rem;
}

.gpt_footer-copyrights p {
    color: #FFF;
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
}

@media screen and (max-width: 850px) {
    .gpt_footer-heading h1 {
        font-size: 44px;
        line-height: 50px;
    }
}

@media screen and (max-width: 550px) {
    .gpt_footer-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }

    .gpt_footer-links div {
        margin: 1rem 0;
    }

    .gpt_footer-btn p {
        font-size: 14px;
        line-height: 20px;
    }
}