.gpt_features-container_feature {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
    margin: 1rem;
}

.gpt_features-container_feature-title {
    flex: 1;
    max-width: 100px;
    margin-right: 2rem;
}

.gpt_features-container_feature-title h1 {
    color: #FFF;
    font-family: var(--font-family);
    font-size: 18px;
    font-weight: 800;
    line-height: 24px;
    letter-spacing: -0.96px;
}

.gpt_features-container_feature-title div {
    width: 38px;
    height: 3px;
    background: var(--gradient-bar);
    box-shadow: 0 4px 4px rgba(0,0,0,0.25);
    margin-bottom: 0.25rem;
}

.gpt_features-container_feature-text {
    flex: 2;
    max-width: 58rem;
    display: flex;
}

.gpt_features-container_feature-text p {
    color: var(--color-text);
    font-family: var(--font-family);
    font-size: 14px;
    font-weight: 500;
    line-height: 24px;
}

@media screen and (max-width: 550px) {
    .gpt_features-container_feature-title h1 {
        font-size: 14px;
        line-height: 22px;
    }

    .gpt_features-container_feature-text p {
        font-size: 12px;
        line-height: 20px;
    }

    .gpt_features-container_feature {
        margin: 1rem 0;
    }
}