.gpt_whatgpt {
    display: flex;
    flex-direction: column;
    padding: 2rem;

    background: linear-gradient(140deg, #0F8CE6F5 0%, #01498C 13%, #095389 85%, #0F8CE6F5 99%);
}

.gpt_whatgpt-feature {
    display: flex;
}

.gpt_whatgpt-feature .gpt_features-conatiner_feature {
    flex: 1;
    margin: 1rem;
    min-width: 210px;
    display: unset;
    flex-direction: column;
}

.gpt_whatgpt-feature .gpt_features-conatiner_feature-text {
    max-width: 700px;
}

.gpt_whatgpt-heading {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 4rem 0 2rem; 
}

.gpt_whatgpt-heading h1 {
    font-family: var(--font-family);
    font-size: 34px;
    line-height: 45px;  
    font-weight: 800;
    max-width: 510px;
}

.gpt_whatgpt-heading p {
    font-family: var(--font-family);
    color: var(--color-subtext);
    font-size: 16px;
    line-height: 30px;
    font-weight: 500;
}

.gpt_whatgpt-container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    margin-top: 2rem;
}

.gpt_whatgpt-container .gpt_features-container_feature {
    flex: 1;
    margin: 1rem;
    min-width: 210px;
    display: unset;
    flex-direction: column;
}

.gpt3__whatgpt3-container .gpt3__features-container_feature-text {
    margin-top: 0.5rem;
}

@media screen and (max-width: 850px) {
    .gpt3__whatgpt3-heading {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem;
    }

    .gpt3__whatgpt3-heading p {
        margin-top: 1rem;
    }
}

@media screen and (max-width: 650px) {
    .gpt3__whatgpt3-feature .gpt3__features-container__feature {
        flex-direction: column;
    }

    .gpt3__whatgpt3-feature .gpt3__features-container_feature-text {
        margin-top: 0.5rem;
    }

    .gpt3__whatgpt3-heading h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 350px) {
    .gpt3__whatgpt3-container .gpt3__features-container__feature {
        margin: 1rem 0;
        min-width: 100%;
    }
}