.gpt_blog {
  display: flex;
  flex-direction: column;  
}

.gpt_blog-heading {
    width: 100%;
    text-align: left;
    margin-bottom: 5rem;
}

.gpt_blog-heading h1 {
    font-family: var(--font-family);
    font-size: 62px;
    font-weight: 800;
    line-height: 75px;
    letter-spacing: -2.48px;
}

.gpt_blog-container {
    display: flex;
    flex-direction: row;
}

.gpt_blog-container_groupA {
    flex: 0.75;
    margin-right: 2rem;
}

.gpt_blog-container_groupB {
    flex: 1;
    display: grid;
    grid-template-columns: repeat(2 ,1fr);
    grid-gap: 2rem;
}

@media screen and (max-width: 1050px) {
    .gpt_blog-container {
     flex-direction: column-reverse;
    }

    .gpt_blog-container_groupA {
        margin: 2rem 0;;
    }

    .gpt_blog-container_groupA .gpt_blog-container_article {
        width: 47%;
    }

    .gpt_blog-container_groupA .gpt_blog-container_article-image {
        height: 200px;
    }
 }

 @media screen and (max-width: 700px) {
    .gpt_blog-heading h1 {
        font-size: 46px;
        line-height: 52px;
    }

   .gpt_blog-container_groupB {
    grid-template-columns: repeat(1,1fr);
   }

   .gpt_blog-container_groupA .gpt_blog-container_article {
    width: 100%;
   }
 }

 @media screen and (max-width: 550px) {
    .gpt_blog-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }
 }