.gpt_header {
    display: flex;
    flex-direction: row;
}

.gpt_header-content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-right: 5rem;
}

.gpt_header-content h1 {
    font-family: var(--font-family);
    font-size: 62px;
    font-weight: 800;
    line-height: 75px;
    letter-spacing: -2.48px;
}

.gpt_header-content p {
    color: #81AFDD;
    font-family: var(--font-family);
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    margin-top: 1.5rem;
}

.gpt_header-content_input {
    width: 100%;
    margin: 2rem 0 1rem;
    display: flex;
}

.gpt_header-content_input input {
    flex: 2;
    width: 100%;
    min-height: 50px;
    font-family: var(--font-family);
    background: var(--color-footer);
    font-size: 20px;
    line-height: 27px;
    border: 1.5rem solid var(--color-footer);
    padding: 0 1rem;
    outline: none;
    color: #fff;

    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}

.gpt_header-content_input button {
    flex: 1;
    width: 100%;
    min-height: 50px;
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 20px;
    line-height: 27px;
    background: var(--color-subtext);
    border: none;
    color: #FFF;
    cursor: pointer;
    outline: none;
    padding: 0 1rem;

    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}

.gpt_header-content_people {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: 2rem;
}

.gpt_header-content_people img {
    width: 181px;
    height: 38px;
}

.gpt_header-content_people p {
    margin: 0 0 0 1rem;
    color: #FFF;
    font-family: var(--font-family);
    font-size: 12px;
    font-weight: 500;
    line-height: 37.611px;
    text-align: center;
}

.gpt_header-image {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.gpt_header-image img {
    width: 100%;
    height: 100%;
}

@media screen and (max-width: 1050px) {
    .gpt_header {
        width: 100%;
        height: 100%;
        flex-direction: column;
    }

    .gpt_header-content {
        margin: 0 0 3rem;
    }
}

@media screen and (max-width: 650px) {
    .gpt_header h1 {
        font-size: 48px;
        line-height: 60px;
    }

    .gpt_header p {
        font-size: 16px;
        line-height: 24px;
    }

    .gpt_header-content_people {
        flex-direction: column;
    }

    .gpt_header-content_people p {
        margin: 0;
    }

    .gpt_header-content_input input,
    .gpt_header-content_input button {
        font-size: 16px;
        line-height: 24px;
    }
}

@media screen and (max-width: 490px) {
    .gpt_header h1 {
        font-size: 36px;
        line-height: 48px;
    }

    .gpt_header p {
        font-size: 14px;
        line-height: 24px;
    }

    .gpt_header-content_input input,
    .gpt_header-content_input button {
        font-size: 12px;
        line-height: 16px;
    }
}